<template>
	<view class="container">
		<form @submit="formSubmit">
			<view class="cu-form-group">
				<view class="title">手机号码</view>
				<input placeholder="请输入手机号" v-model="mobile" name="mobile" />
				<view class="cu-capsule radius">
					<view class="cu-tag bg-blue ">+86</view>
					<view class="cu-tag line-blue">中国大陆</view>
				</view>
			</view>
			<view class="cu-form-group">
				<view class="title">邮箱</view>
				<input placeholder="请输入邮箱" v-model="email" name="email" />
			</view>
			<!-- #ifndef MP-ALIPAY -->
			<view class="cu-form-group">
				<view class="title">地址</view>
				<pick-regions :default-regions="defaultRegions" @getRegions="handleGetRegions">{{ regionsName }}</pick-regions>
			</view>
			<!-- #endif -->
			<view class="cu-form-group">
				<view class="title">生日</view>
				<picker mode="date" v-model="birth" name="birth" start="2015-09-01" end="2020-09-01" @change="DateChange">
					<view class="picker">{{ birth }}</view>
				</picker>
			</view>
			<view class="form_btn"><button class="sub_btn" type="primary" form-type="submit">提交</button></view>
		</form>
	</view>
</template>

<script>
import pickRegions from '@/components/pick-regions/pick-regions.vue';
const api = require('@/common/api.js');
const util = require('@/common/util.js');
const auth = require('@/common/auth.js');

export default {
	components: {
		pickRegions
	},
	data() {
		return {
			mobile: '',
			email: '',
			birth: '2018-12-25',
			regionsName: '',
			selectCode: [],
			defaultRegions: [],
			index: -1
		};
	},
	onLoad() {
		this.loadUser();
	},
	methods: {
		async loadUser() {
			var that = this;
			var res = await api.loadUser(auth.getUId());
			console.log('res：' + JSON.stringify(res));
			if (res.code === 1) {
				var user = res.data;
				that.mobile = user.mobile;
				that.email = user.email;
				that.birth = user.birth;
				that.regionsName = user.city_names;

				that.defaultRegions[0] = util.isEmpty(user.city_code1) ? '' : user.city_code1;
				that.defaultRegions[1] = util.isEmpty(user.city_code2) ? '' : user.city_code2;
				that.defaultRegions[2] = util.isEmpty(user.city_code3) ? '' : user.city_code3;
			}
		},
		// 获取选择的地区
		handleGetRegions(regions) {
			this.regions = regions;

			var selectCode = this.regions.map(item => item.code).join(',');
			var selectName = this.regions.map(item => item.name).join(',');
			console.log('地址code:' + JSON.stringify(selectCode));
			console.log('地址name:' + JSON.stringify(selectName));

			this.selectCode = selectCode;
			this.regionsName = selectName;
		},
		DateChange(e) {
			this.birth = e.detail.value;
		},
		RegionChange(e) {
			this.region = e.detail.value;
		},
		async formSubmit(e) {
			var that = this;
			var formdata = e.detail.value;
			formdata.selectCode=that.selectCode;
			formdata.regionsName=that.regionsName;
			
			console.log("提交:"+JSON.stringify(formdata))
		
			let res = await api.fullUserInfo(formdata);
		
			if (res.code == 1) {
				util.showToast('修改成功!');
				setTimeout(function() {
					that.goMe();
				}, 100);
			} else {
				util.showToast(res.message);
			}
		},
		goMe(){
			uni.navigateTo({
				url: '../me/me'
			});
		},
	}
};
</script>

<style>
page {
	height: 100%;
	width: 100%;
	background: #f8f8f8;
}
.container {
	padding: 10px;
}
.cu-form-group .title {
	min-width: calc(4em + 15px);
}
.form_btn {
	display: flex;
	flex-direction: row;
	margin-top: 20px;
}
.sub_btn {
	height: 40px;
	width: 80%;
}
</style>
